.playground {
    width: 75%;
    position: absolute;
    right: 0;
    top: 0;
    min-width: 500px;
}

.playground > div {
    margin: 1em;
    padding: 1em;
}

.label {
    font-size: 1.4em;
}

.controls {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 20%;
    background-color: #eee;
    overflow-y: scroll;
    min-width: 300px;

}

.controls > div {
    border-bottom: thin solid teal;
    margin: .2em;
    padding: .2em;
}

.controls label {
    font-size: 1.2em;
}